.tips {

    padding: 20px;
    background-color: #FFF3CD; /* 设置一个浅色背景区分内容 */
    overflow-y: auto; /* 如果内容溢出，允许滚动 */
    margin-left: 100px; /* 为内容栏设置左边距，确保不被导航栏遮挡 */
    margin-top: 20px;
    border-radius: 15px; /* 设置圆角 */
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2); /* 添加轻微阴影，增强视觉效果 */
    width: 70vw;
    height: auto;
}

.tips h1 {
    font-size: 40px;
    display: flex;
    justify-content: center;
    padding: 6px 15px 8px 15px;
    word-wrap: break-word;
}

.tips words{
    display: block;
    white-space: pre-line;
    font-size: 24px;
}

.container_img{
    width: 600px;
}

@media (max-width: 600px) {
    .tips {

        padding: 20px;
        background-color: #FFF3CD; /* 设置一个浅色背景区分内容 */
        overflow-y: auto; /* 如果内容溢出，允许滚动 */
        margin-left: 70px; /* 为内容栏设置左边距，确保不被导航栏遮挡 */
        margin-top: 20px;
        border-radius: 15px; /* 设置圆角 */
        box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2); /* 添加轻微阴影，增强视觉效果 */
        width: 65vw;
    }
    
    .tips h1 {
        font-size: 40px;
        display: flex;
        justify-content: center;
        padding: 6px 15px 8px 15px;
        word-wrap: break-word;
    }
    
    .tips words{
        display: block;
        white-space: pre-line;
        font-size: 24px;
    }
    
    .container_img{
        width: 600px;
    }
}